/*===========================
  2.1 Header Area
  ===========================*/

.title-container {
    /* margin: 1em 0 0.5em 0; */
    width: 450px;
	font-weight: 600;
	font-family: 'Titillium Web', sans-serif;
	position: relative;  
	font-size: 36px;
	line-height: 40px;
	/* padding: 15px 15px 15px 15%; */
	color: #355681;
	box-shadow: 
		inset 0 0 0 1px rgba(53,86,129, 0.4), 
		inset 0 0 5px rgba(53,86,129, 0.5),
		inset -285px 0 35px white;
	border-radius: 0 10px 0 10px;
    background: no-repeat url(/shugaring-site/static/media/eyes2.614f81d8.jpg), linear-gradient(to right, #fff, #fff) center left;
}

.title-container__name {
    color: #fff;
    font-size: 50px;
    text-transform: uppercase;
    font-weight: 700;
    background: linear-gradient(to right, #e91e63 10%, rgb(226, 52, 32) 50%, rgb(204, 75, 24) 60%);
    background-size: auto auto;
    background-clip: border-box;
    background-size: 200% auto;
    color: #fff;
    background-clip: text;
    /* text-fill-color: transparent; */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: textclip 2.5s linear infinite;
    display: inline-block;
}
@keyframes textclip {
	to {
		background-position: 200% center;
	}
}

.mobile-menu {
    list-style: none;
    display: flex;
    flex-flow: row nowrap;
}
.mobile-menu li {
    padding: 18px 10px;
}

.header-area {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 99;
    transition: all 0.3s ease 0s;
}

.header-area.header_fix {
    background: #FA8072;
    z-index: 9;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3);
}

.menu-area {
    display: flex;
}

.logo {
    margin-top: 27px;
}

.logo a {
    display: inline-block;
}

.logo a img {
    max-width: 195px;
}

.main-menu {
    text-align: right;
}

.main-menu nav ul {}

.main-menu nav ul li {
    display: inline-block;
}

.main-menu nav ul li a {
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    display: block;
    padding: 33px 12px;
    letter-spacing: 0;
    text-transform: capitalize;
    text-decoration: none;
}

.main-menu nav ul li a:hover,
.main-menu nav ul li.active a {
    color: #444;
}

@media (max-width:767px){.hidden-xs{display:none!important}}
@media (min-width:768px) and (max-width:995px){.hidden-sm{display:none!important}}
@media (min-width:768px) and (max-width:995px){.visible-sm{display:block!important}}
@media (max-width:767px){.visible-xs{display:block!important}}

/*mobile menu*/

.sidebar_menu {
    width: 2rem;
    height: 2rem;
    position: fixed;
    top: 15px;
    right: 20px;
    z-index: 20;
    display: none;
}

@media (max-width:995px) {
    .sidebar_menu {
        display: flex;
        justify-content: space-around;
        flex-flow: column nowrap;
    }
}

.slicknav_menu .slicknav_menutxt {
    display: none;
}

.slicknav_menu {
    background: transparent;
}

.slicknav_btn {
    background-color: transparent;
    position: relative;
    margin-top: -54px;
}

.slicknav_menu .slicknav_icon-bar {
    width: 28px;
    height: 2px;
    margin-bottom: 5px;
    transition: all 0.3s ease 0s;
}

.slicknav_menu .slicknav_open .slicknav_icon-bar:nth-child(1) {
    transform: rotate(-45deg)translate(-6px, 0px);
}

.slicknav_menu .slicknav_open .slicknav_icon-bar:nth-child(2) {
    opacity: 0;
}

.slicknav_menu .slicknav_open .slicknav_icon-bar:nth-child(3) {
    transform: rotate(45deg)translate(-10px, -3px);
}

.slicknav_nav a:hover {
    border-radius: 0;
}

